import React, { Component } from 'react';
import { 
	Panel,
	PanelHeader,
	Group,
	Div,
	Button,
	Card,
 } from '@vkontakte/vkui';
import PropTypes from 'prop-types';
import './Intro.css';

var score = 0;
function clickPlus() {
  score++;
  console.log(score);
  document.getElementById("plus").innerHTML = score;
}
function clickMinus() {
  score = 0;
  console.log(score);
  document.getElementById("plus").innerHTML = score;
}

const Intro = ({ id, goGame }) => (
	<Panel id={id} centered={true}>
		<PanelHeader>Brained</PanelHeader>
		<Group separator="hide">
			<Div className="textInfo">
				<span id="plus">0</span>
			</Div>
			<Div className="parentDiv">
				<Card className="moneyInfo">
					
				</Card>
				<Card className="moneyInfo" style={{ marginLeft: 120 }}>
					
				</Card>
			</Div>
				<Group separator="hide">
						<Card className="daily">
							
						</Card>
				</Group>
		</Group>
			<Group>
				<Div className="parentDiv">
					<Button className="clickBtn" onClick={ () => clickPlus()} style={{ marginRight: 20 }} >
						1
					</Button>
					<Button className="clickBtn" onClick={ () => clickMinus()} >
						2
					</Button>
					<Button className="clickBtn" style={{ marginLeft: 20 }} onClick={goGame} data-to='game'>
						3
					</Button>
				</Div>
				<Div className="parentDiv">
					<Button className="clickBtn" style={{ marginRight: 20 }} >
						4
					</Button>
					<Button className="clickBtn">
						5
					</Button>
					<Button className="clickBtn" style={{ marginLeft: 20 }} >
						6
					</Button>
				</Div>
				<Div className="parentDiv">
					<Button className="clickBtn" style={{ marginRight: 20 }} >
						7
					</Button>
					<Button className="clickBtn">
						8
					</Button>
					<Button className="clickBtn" style={{ marginLeft: 20 }} >
						9
					</Button>
				</Div>
			</Group>
	</Panel>
);
Intro.propTypes = {
	id: PropTypes.string.isRequired,
	goGame: PropTypes.func.isRequired,
};
export default Intro;
